/*BOT*/

#botDiv { background-color:#242424;background-size:cover;background-repeat:no-repeat;background-position:bottom right;background-attachment:fixed;position:relative;z-index: 1;}
#botDiv .overlay{z-index: -1;}
#botDiv .footerDiv{background:#202020;color:#fff;font-size:0.75rem; padding:15px 50px;display:flex;align-content:space-between;align-items:center;justify-content:center;border-top:4px #ff617f solid;}
#botDiv .footerDiv .foot-item{vertical-align:middle;flex:1;}
#botDiv .footerDiv .foot-item.designBy{text-align: right;}
#botDiv .footerDiv .foot-item.designBy > *{vertical-align:middle;}

#botDiv .footerDiv .foot-item.copyright{text-align: center;}
#botDiv .footerDiv .foot-item.reactSol{text-align: left;}
#botDiv .footerDiv .foot-item span.sep{vertical-align:baseline;}

#botDiv .findMe{color:#fff;font-size:0.75rem;padding:150px 15px 150px;position:relative;z-index:3; vertical-align:middle; border-top:5px #ebd7db solid;}
#botDiv .findMe h2{color:#fff; letter-spacing: 4px;margin:0 0 5px;}
#botDiv .findMe h3{color:#fff; letter-spacing: 3px;margin:0px; text-transform: none; font-weight: 400; font-size: 0.85rem;}
#botDiv .findMe .msgBody{letter-spacing: 4px; display: block;margin:15px 0 0;}
#botDiv .specialThanks .msgBody{letter-spacing: 1.5px; display: block;}
#botDiv .specialThanks h4{color:#fff;}
/*END BOT*/



/*IOS*/
@supports (-webkit-touch-callout: none) {
    #botDiv { background-attachment:scroll;}
}



@media screen and (max-width: 1280px) {
    #botDiv .findMe .msgBody{letter-spacing: 2.5px;}
    #botDiv .specialThanks .msgBody{letter-spacing:1.25px;}
    #botDiv .footerDiv{ padding:10px 25px;border-top-width: 2px;}
    #botDiv .footerDiv .foot-item.copyright{text-align: left;}
}

@media screen and (max-width: 769px) {
    #botDiv .findMe{border-top: 3px #ebd7db solid;}
    #botDiv { background-attachment:scroll;}  /*support for mobile feels edgy, hence disable */
}



@media screen and (max-width: 640px) {
    #botDiv .footerDiv{display: block;}
    #botDiv .footerDiv .foot-item.copyright{text-align:center;padding: 10px;}
    #botDiv .footerDiv .foot-item.designBy{display:none;}
    #botDiv .footerDiv .foot-item.copyright a{display: inline-block;}
}

@media screen and (max-width: 512px) {
    #botDiv .findMe h2{font-size:1.75rem;}
}


@media screen and (max-width: 480px) {
    #botDiv .footerDiv{padding:15px 15px;}

}

